<template>
  <div id="app">
    <div class="header">
      <div class="logo"></div>
      <el-input
        placeholder="输入产品关键字"
        v-model="filterText">
      </el-input>
    </div>
    <div class="content">
      <div class="tree-wrap">
        <template v-if="itemList.length > 0">
          <p class="total">总&nbsp;<b>{{itemList.length}}</b></p>
          <ul>
            <li v-for="(item, idx) in itemList" :key="idx">
              <i class="el-icon-document"></i>
              <a :href="`../static/${idx + 1}.html`" target="_blank">{{ item.name }}</a>
            </li>
          </ul>
        </template>
        <p class="no-data" v-else>暂无数据</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      filterText: '',
      fileList: [{
        name: '电泳双轨自行输送小车作业安全操作规程.docx',
      },{
        name: '侧滑试验台操作规程10.30.doc',
      },{
        name: 'ABS制动台操作规程.docx',
      },{
        name: '底盘间隙仪操作规程.docx',
      },{
        name: '四轮定位操作规程10.30.docx',
      }, {
        name: '车速表检验台操作规程10.30.doc',
      }, {
        name: '声级计操作规程10.30.doc',
      }, {
        name: '轮（轴）重台操作规程10.30.doc',
      }],
      itemList: []
    }
  },
  watch: {
    filterText(val) {
      if(val !== '') {
        this.itemList = this.fileList.filter(item => item.name.indexOf(val) > -1)
      }else {
        this.itemList = this.fileList
      }
      
    }
  },
  created() {
    this.itemList = this.fileList
  }
}
</script>

<style lang="less">
  html,body{
    width: 100%;
    height: 100%;
  }
  html,body,div,p,h3,ul,li{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  #app {
    height: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #333;
    background: #f4f4f4;
    .header{
      height: 85px;
      padding: 25px 2%;
      background: #fff;
      .logo{
        width: 100px;
        height: 35px;
        background: url(./assets/logo.png) left no-repeat;
        background-size: contain;
        float: left;
      }
      .el-input{
        width: 60%;
        font-size: 14px;
        height: 35px;
        float: right;
        .el-input__inner{
          height: 100%;
          line-height: 35px;
        }
      }
    }
    .content{
      width: 100%;
      height: calc(100% - 85px);
      padding: 1%;
      .tree-wrap{
        width: 100%;
        height: 100%;
        background: #fff;
        border-radius: 8px;
        padding: 32px 4%;
        font-size: 16px;
        .total{
          height: 40px;
          line-height: 24px;
          padding-bottom: 16px;
          border-bottom: 1px solid #ddd;
        }
        ul{
          overflow: auto;
          height: calc(100% - 40px);
        }
        ul li{
          list-style: none;
          height: 48px;
          line-height: 48px;
          margin-bottom: 4px;
          border-bottom: 1px solid #eee;
          padding: 0 16px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          cursor: pointer;
          a{
            padding-left: 4px;
            text-decoration: none;
            color: #606266;
          }
        }
        .no-data{
          height: 60px;
          line-height: 60px;
          color: #909399;
          text-align: center;
          font-size: 14px;
        }
      }
    }
  }
</style>


